.bs-label {
    font-size: 80%;
    line-height: 1;
    display: inline;
    padding: 4px;
    text-align: center;
    text-transform: uppercase;
    vertical-align: baseline;
    white-space: nowrap;
    border-radius: 2px;
    font-weight: 700
}

.bs-label[href]:focus, .bs-label[href]:hover {
    cursor: pointer;
    text-decoration: none;
    color: #fff
}

.bs-label:empty {
    display: none
}

.btn .bs-label {
    position: relative;
    top: -1px
}

.label-default {
    background-color: #999
}

.label-default[href]:focus, .label-default[href]:hover {
    background-color: gray
}

.badge, .bs-badge {
    font-size: 11px;
    font-weight: 700;
    line-height: 19px;
    display: inline-block;
    min-width: 20px;
    height: 20px;
    padding: 0 4px 0 5px;
    text-align: center;
    vertical-align: baseline;
    white-space: nowrap;
    color: #fff;
    border-radius: 10px
}

.badge-small {
    min-width: 10px;
    height: 10px
}

.bs-badge:empty {
    display: none
}

.btn .bs-badge {
    position: relative;
    top: -2px
}

.bs-badge.badge-absolute {
    position: absolute;
    z-index: 5;
    top: -10px;
    left: -15px
}

.bs-badge.badge-absolute.float-right {
    right: -15px;
    left: auto
}

.status-badge {
    position: relative;
    display: inline-block
}

.status-badge .small-badge {
    position: absolute;
    right: 1px;
    bottom: 1px
}

.small-badge {
    overflow: hidden;
    width: 12px;
    height: 12px;
    padding: 0;
    border: 2px solid #fff!important;
    border-radius: 20px
}